<template>

  <div id="app">

    <div v-if = "this.$store.state.user.oauth && this.$store.state.user.badge != 'nouser'">
      <router-view class="view"></router-view>
    </div>

    <div v-if = "this.$store.state.user.oauth && this.$store.state.user.badge == 'nouser'" class="weui-msg">
      <div class="weui-msg__icon-area"> <i class="weui-icon-warn weui-icon_msg"></i>
      </div>
      <div class="weui-msg__text-area">
        <h2 class="weui-msg__title">身份认证失败</h2>
        <p class="weui-msg__desc">请退出重新进入该页面</p>
        <p class="weui-msg__desc">或者您没有从企业号进入</p>
      </div>
      <div class="weui-msg__extra-area">
        <p class="weui-footer__links">
          <a href="#" class="weui-footer__link">奥克斯微信企业号</a>
        </p>
        <p class="weui-footer__text">Copyright © 2015-2017 open.auxgroup.com</p>
      </div>
    </div>

    <div v-if = "!this.$store.state.user.oauth">
      <div id="loadingToast">
        <div class="weui-mask_transparent"></div>
        <div class="weui-toast"> <i class="weui-loading weui-icon_toast"></i>
          <p class="weui-toast__content">数据加载中</p>
        </div>
      </div>
    </div>

  </div>

</template>

<script>

  import authServer from './scripts/authToken.js';

  //import wx from 'weixin-js-sdk';
  
  export default {

     data() {

        return {
          ret:{},
        }
        
      },

      computed: {

        // hideLoading(){
        //   if(this.$store.state.user.oauth || this.$store.state.user.token != 'init-token')
        //    return true;
        //   else return true;
        // }

      },

      // mounted : function () {

      //     var self = this;

      //     authServer.auth2js().then(function(res) {

      //     self.ret = res.data;

      //     wx.config({
      //       debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
      //       appId: 'wx449f1ab484b0d0e5', // 必填，企业号的唯一标识，此处填写企业号corpid
      //       timestamp: self.ret.timestamp, // 必填，生成签名的时间戳
      //       nonceStr: self.ret.nonceStr, // 必填，生成签名的随机串
      //       signature: self.ret.signature,// 必填，签名，见附录1
      //       jsApiList: ['hideMenuItems'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
      //     });

      //     wx.ready(function(){
      //        wx.hideOptionMenu();
      //     });

      //   });
        

      // },

      created : function () {

        var self = this;

        let token = authServer.getCookie('wechat-token');
        console.log(this.$store.state.user.oauth);
        if(! this.$store.state.user.oauth){
          
          if("no-token" != token){

             authServer.decToken(token).then(function(res) {
                self.$store.commit('oauthUser',res.data.extattr[2].value);
              } , function(res) {
                console.log(res);
              });
           
           }else{
              this.$store.commit('oauthUser', 'nouser');
           }
        }

      },

    }

</script>

<style>

.weui-media-box:before{
    border-top: 0px solid #E5E5E5;
}

</style>


