const state = {
  
  badge:'',
  oauth : false ,//是否认证
  token : 'init-token',
  wordscount : 0,
  wordslineBreak: false,
}

const mutations = {
  
  oauthUser(state , badge) {
    state.badge = badge;
    state.oauth = true;
  },

  saveToken(state , token){
  	state.token = token;
  },

  saveWordscount(state , wordscount){
    state.wordscount = wordscount;
  },

  wordslineBreak(state , tag){
    state.wordslineBreak = tag;
  }

}

export default {
  state,
  mutations
}


