import * as types from './mutation-types'

const state = {

  meetings : [], //所有我参与过的meeings数据  [{'id1',meeting1} , {'id2',meeting2}]

  participants : [], //所有登陆人写过和正在写的数据

  raters : [], //所有登陆人正在评价和已评的数据

}

const mutations = {

 [types.ADD_TO_MEETINGS] (state, meeting) {
    
    const record = state.meetings.find(p => p.id === meeting.id)
    
    if (!record) {
      state.meetings.push({
        id,
        info : meeting
      })
    }
  },
}

export default {
  state,
  mutations
}


