const state = {
	score:[12,24,18,6],
}
const mutations = {

	changeScore(state,score) {
		state.score.splice(0,1,score.score1);
		state.score.splice(1,1,score.score2);
		state.score.splice(2,1,score.score3);
		state.score.splice(3,1,score.score4);
	},
	addScore(state,level) {

		state.score.splice(level-1,1,++state.score[level-1]);
		switch(level) {
			case 1:
				if(state.score[level-1] > 20) {
					state.score[level-1] = 0;
				}
				break;
			case 2:
				if(state.score[level-1] > 40) {
				state.score[level-1 ]= 0;
				}
				break;
			case 3:
				if(state.score[level-1] > 30) {
				state.score[level-1] = 0;
				}
				break;
			case 4:
				if(state.score[level-1] > 10) {
				state.score[level-1] = 0;
				}
			default:
				break;
		}
		
  	 },
  	reduceScore(state,level) {

  		state.score.splice(level-1,1,--state.score[level-1]);
		switch(level) {
			case 1:
				if(state.score[level-1] < 0){
					state.score[level-1] = 20;
				}
				break;
			case 2:
				if(state.score[level-1] < 0){
					state.score[level-1] = 40;
				}
				break;
			case 3:
				if(state.score[level-1] < 0){
					state.score[level-1] = 30;
				}
				break;
			case 4:
				if(state.score[level-1] < 0){
					state.score[level-1] = 10;
				}
			default:
				break;
		}
  	},
}
export default {
  state,
  mutations
}


