import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/auxeditor'
    }, {
      path: '/page',
      component: resolve => require(['../rate/Page.vue'], resolve)
    }, {
      path: '/rate',
      component: resolve => require(['../rate/Rate.vue'], resolve)
    }, {
      path: '/success',
      component: resolve => require(['../rate/SuccessPage.vue'], resolve)
    }, {
      path:'/checkrate',
      component:resolve => require(['../rate/CheckRate.vue'], resolve)
    },{
      path: '/write',
      component: resolve => require(['../rate/Write.vue'], resolve)
    },{
      path: '/raterlist',
      component: resolve => require(['../rate/RaterList.vue'], resolve)
    },{
      path: '/submit',
      component: resolve => require(['../rate/Submit.vue'], resolve)
    },{
      path: '/finishscore',
      component: resolve => require(['../rate/FinishScore.vue'], resolve)
    },{
      path: '/catalog',
      component: resolve => require(['../rate/Catalog.vue'], resolve)
    },{
      path: '/text',
      component: resolve => require(['../rate/Text.vue'], resolve)
    },{
      path: '/raterhome',
      component: resolve => require(['../rate/RaterHome.vue'], resolve)
    },{
      path: '/ratermain',
      component: resolve => require(['../rate/RaterMain.vue'], resolve)
    },{
      path: '/score',
      component: resolve => require(['../rate/score.vue'], resolve)
    },
    {
      path: '/auth',
      component: resolve => require(['../rate/Auth.vue'], resolve)
    },


    // {
    //   path: '/werate/pageContent',
    //   component: resolve => require(['../rate/PageContent.vue'], resolve)
    // },
    {
      path: '/auxeditor',
      component: resolve => require(['../auxeditor/editorContent.vue'], resolve)
    },
    //网页单独展示
    {
      path: '/meetingRecords',
      component: resolve => require(['../auxeditor/meetingRecords.vue'], resolve)
    },


    {
      path: '/display/meetings',
      component: resolve => require(['../rate/meetings/meetingList.vue'], resolve)
    },

    {
      path: '/display/participants',
      component: resolve => require(['../rate/meetings/partiList.vue'], resolve)
    },

    {
      path: '/display/participant',
      component: resolve => require(['../rate/meetings/participant.vue'], resolve)
    },


  ]

})


